footer {
  background-color: #f7f7f7;
  padding-bottom: 30px;
  padding-top: 30px;
}
.container__footer {

  align-items: center;
  margin-left: auto;
  margin-right: auto;

   text-align: center;
 
  
  @media screen and (min-width: 768px) {
       display: flex;
       justify-content: center;
       
    }
}

.footer__rights {

  color: #545454;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 16px;
    line-height: 1.17;
  }
}


.footer__firstline {
  align-items: center;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

.footer__rights {
  margin: 0;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
  @media screen and (min-width: 768px) {
    padding-bottom: 0;
    padding-right: 3px;
  }
}
.footer__team {
  position: relative;
  margin: 0;

.footer__group{

  display: flex;
    @media screen and (max-width: 767px) {
    margin-top: 10px;
    }
}
.footer__item {

  padding-left: 10px;
  padding-right: 10px;
  color: #ff6b08;

  margin-left: 10px;
  margin-right: 10px;

}
.footer__modal {
  padding-left: 10px;
  color: #545454;
  cursor: pointer;
  text-decoration: underline;
}
.footer__modal:hover {
  color: #ff6b08;
}
.footer__modal:focus {
  color: #ff6b08;
}
}